.is-header {
  background-color: #b8cbc8;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.35);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: $header-padding-mobile;

  @include tablet {
    padding: $header-padding-tablet;
  }

  @include desktop {
    padding: $header-padding-desktop;
  }

  @include widescreen {
    padding: $header-padding-widescreen;
  }

  img {
    height: $header-img-height-mobile;

    @include tablet {
      height: $header-img-height-tablet;
    }

    @include laptop {
      height: $header-img-height-laptop;
    }

    @include desktop {
      height: $header-img-height-desktop;
    }

    @include widescreen {
      height: $header-img-height-widescreen;
    }
  }
}
