@mixin mobile-max {
  @media screen and (max-width: #{$tablet - 1}) {
    @content;
  }
}

@mixin tablet {
  @media screen and (min-width: #{$tablet}) {
    @content;
  }
}

@mixin laptop {
  @media screen and (min-width: #{$laptop}) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: #{$desktop}) {
    @content;
  }
}

@mixin widescreen {
  @media screen and (min-width: #{$widescreen}) {
    @content;
  }
}
