@import "./variables";
@import "./mixins";

@import "./scaffolding";
@import "./base";

@import "./fonts";
@import "./header";
@import "./slider";
@import "./tooltip";

.is-wrapper {
  position: relative;
}

.is-scene-container {
  width: 100%;
  height: calc(100vh - 90px);

  @media only screen and (min-device-width: 414px) and (max-device-height: 896px) and (-webkit-device-pixel-ratio: 2) {
    height: calc(100vh - 150px);
  }

  @media only screen and (min-device-width: 375px) and (max-device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
    height: calc(100vh - 150px);
  }

  @media only screen and (min-device-width: 375px) and (max-device-height: 667px) and (-webkit-device-pixel-ratio: 2) {
    height: calc(100vh - 110px);
  }

  @include tablet {
    height: calc(
      100vh - #{$header-img-height-tablet} - #{$header-padding-tablet} * 2
    );
  }

  @include laptop {
    height: calc(
      100vh - #{$header-img-height-laptop} - #{$header-padding-laptop} * 2
    );
  }

  @include desktop {
    height: calc(
      100vh - #{$header-img-height-desktop} - #{$header-padding-desktop} * 2
    );
  }

  @include widescreen {
    height: calc(
      100vh - #{$header-img-height-widescreen} - #{$header-padding-widescreen} *
        2
    );
  }

  .swiper-wrapper {
    position: relative;

    @include mobile-max() {
      transform: none !important;
    }

    & > div {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
  }
}

.is-thumb-container {
  position: absolute;
  bottom: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  transition: transform 0.25s ease-in-out;
  will-change: transform;
  z-index: 1;

  &.is-hidden {
    transform: translateY($thumb-pad-mobile * 2 + $thumb-height-mobile);

    @include tablet() {
      transform: translateY($thumb-pad-tablet * 2 + $thumb-height-tablet);
    }

    @include laptop() {
      transform: translateY($thumb-pad-laptop * 2 + $thumb-height-laptop);
    }

    @include desktop() {
      transform: translateY($thumb-pad-desktop * 2 + $thumb-height-desktop);
    }

    @include widescreen() {
      transform: translateY($thumb-pad-widescreen * 2 + $thumb-height-desktop);
    }

    .is-btn-toggle {
      img {
        transform: rotate(0);
      }

      &:hover {
        img {
          @include laptop() {
            transform: scale(1.05);
          }
        }
      }
    }
  }

  .swiper-container {
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.thumbnails-header {
  display: flex;
  margin-left: $thumb-pad-mobile;

  @include tablet() {
    margin-left: $thumb-pad-tablet * 2;
  }

  @include laptop() {
    margin-left: 45px;
  }

  @include desktop() {
    margin-left: 65px;
  }

  @include widescreen() {
    margin-left: 85px;
  }
}

.is-btn {
  position: relative;
  z-index: 1;
  height: 30px;
  width: 50px;
  border-radius: 5px 5px 0 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;

  @include tablet() {
    height: 30px;
    width: 55px;
  }

  @include laptop() {
    width: 41px;
    height: 24px;
    cursor: pointer;
    margin-right: 15px;
  }

  @include desktop() {
    width: 65px;
    height: 38px;
    margin-right: 23px;

    &:hover {
      img {
        transform: scale(1.05);
      }
    }

    img {
      transition: transform 0.15s ease-in-out;
    }
  }

  @include widescreen() {
    width: 85px;
    height: 50px;
    margin-right: 30px;
  }
}

.icon-arrow {
  width: 40%;

  @include laptop() {
    width: 18px;
  }

  @include desktop() {
    width: 28px;
  }

  @include widescreen() {
    width: 36px;
  }
}

.icon-vr {
  width: 50%;

  @include laptop() {
    width: 25px;
  }

  @include desktop() {
    width: 39px;
  }

  @include widescreen() {
    width: 50px;
  }
}

.is-btn-toggle {
  img {
    transform: rotate(180deg);
  }

  @include laptop() {
    &:hover {
      img {
        transform: scale(1.05) rotate(180deg);
      }
    }
  }
}

.is-thumb-wrapper {
  display: flex;
  padding: $thumb-pad-mobile 0;
  white-space: nowrap;
  overflow: scroll;

  @include tablet() {
    padding: $thumb-pad-tablet 0;
  }

  @include laptop() {
    padding: $thumb-pad-laptop 0;
  }

  @include desktop() {
    padding: $thumb-pad-desktop 0;
  }

  @include widescreen() {
    padding: $thumb-pad-widescreen 0;
  }

  &:after {
    content: "q";
    display: block;
    width: $thumb-pad-mobile;
    opacity: 0;
    visibility: hidden;

    @include tablet() {
      width: $thumb-pad-tablet;
    }

    @include desktop() {
      width: $thumb-pad-desktop;
    }
  }

  p {
    margin: 0;
    padding: 2px 0;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 12px;
    line-height: 1.25;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.65);
    text-align: center;

    @include desktop {
      padding: 3px 0;
      font-size: 14px;
      line-height: 1.25;
    }
  }
}

@mixin thumb-size($width, $height) {
  width: $width;
  min-width: $width;
  height: $height;
}

.is-thumb {
  position: relative;
  @include thumb-size($thumb-width-mobile, $thumb-height-mobile);
  margin-right: $thumb-pad-mobile;
  border: 4px solid transparent;
  transition: border-color 0.15s ease-in-out;

  @include tablet() {
    @include thumb-size($thumb-width-tablet, $thumb-height-tablet);
    margin-right: $thumb-pad-tablet;
  }

  @include laptop() {
    @include thumb-size($thumb-width-laptop, $thumb-height-laptop);
    margin-right: $thumb-pad-laptop;
  }

  @include desktop() {
    @include thumb-size($thumb-width-desktop, $thumb-height-desktop);
    margin-right: $thumb-pad-desktop;
  }

  @include widescreen {
    margin-right: $thumb-pad-widescreen;
  }

  &:first-child {
    margin-left: $thumb-pad-mobile;

    @include tablet() {
      margin-left: $thumb-pad-tablet;
    }

    @include desktop() {
      margin-left: $thumb-pad-desktop;
    }
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover,
  &.is-active {
    border-color: #b8cbc8;
  }

  @include laptop() {
    cursor: pointer;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
}
